import SwBase from '../Base.vue'
import SwCol from '../grid/Col.vue'
export default {
    name: 'SwListCol',
    extends: SwBase,
    components: {SwCol},
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'list-col'
         * */
        classPrefix: {
            type: String,
            default: 'list-col'
        },
        /**
         * 
         * @property heading
         * @type Boolean
         * */
        heading: Boolean,
        /**
         * 
         * @property right
         * @type Boolean
         * */
        tail: Boolean
    }
}



