/**
 * SearchBar<br/>
 * - buttonText
 *
 * 
 * - buttonText:  <br/>
 * - placeholder: 
 *
 * 
 * - inputchange: (value,evt) <br/>
 * - focus:  <br/>
 * - blur:  <br/>
 * - search: (value,evt) <br/>
 * - click: (value,evt)
 *
 * @class SearchBar
 * @module 
 * @extends Component
 * @constructor
 * @since 0.0.1
 * @demo search|search.js {}
 * @show true
 * */
import SwBase from '../Base.vue'
import SwInput from '../input/Input.vue'
import SwButton from '../button/Button.vue'
export default {
    name: 'SwSearch',
    extends: SwBase,
    components: {
        SwButton,
        SwInput
    },
    data(){
        return {
            focus: false,
            currentValue: this.value
        }
    },
    methods:{
        handleInput(value,evt){
            this.currentValue = value
            this.$emit('input',value,evt)
            this.$emit('change',value,evt)
        },
        handleFocus(){
            this.focus = true
            this.$emit('focus')
        },
        handleBlur(){
            this.timer = setTimeout(()=>{
                this.focus = false
            })
            this.$emit('blur')
        },
        handleKeyDown(evt){
            this.$emit('search',this.currentValue,evt)
        },
        handleButtonClick(evt){
            this.$emit('click',this.currentValue,evt)
        }
    },
    watch: {
        value(val){
            this.currentValue = val
        }
    },
    beforeDestroy(){
        this.timer && clearTimeout(this.timer)
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'search-bar'
         * */
        classPrefix: {
            type: String,
            default: 'search-bar'
        },
        /**
         * 
         * @property buttonText
         * @type String
         * @default ''
         * */
        buttonText: {
            type: String,
            default: ''
        },
        /**
         * 
         * @property placeholder
         * @type String
         * @default ''
         * */
        placeholder: {
            type: String,
            default: ''
        },
        /**
         * 
         * @property value
         * @type
         * @default
         * */
        value: null
    }
}



