/**
 * <br/>
 * - column1-8
 * - Image
 *
 * 
 * - column:1<br>
 * ```code
 * <sw-image-list :column="2">
 *    <dl   v-for="(url,index) in urls"
 *          class="ph-image-item"
 *          :key="index">
 *       <dt>
 *            <sw-image  :src="url"
 *                       :defaultSrc="defaultSrc"
 *                       lazy></sw-image>
 *        </dt>
 *        <dd>
 *            <p class="ph-image-title"></p>
 *            <p class="ph-image-desp"></p>
 *        </dd>
 *    </dl>
 *</sw-image-list>
 * ```
 *
 * @class ImageList
 * @module 
 * @extends Component
 * @constructor
 * @since 0.0.1
 * @demo imagelist|image-list.js {}
 * @show true
 * */
import SwBase from '../Base.vue'
export default {
    name: 'SwImageList',
    extends: SwBase,
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'image-list'
         * */
        classPrefix: {
            type: String,
            default: 'image-list'
        },
        /**
         * 
         * @property column
         * @type Number
         * @default 1
         * */
        column: {
            type: Number,
            default: 1
        }
    }
}



