
import SwBase from '../Base.vue'
export default {
    name: 'SwRow',
    extends: SwBase,
    render: function(createElement){
        return createElement(
            this.componentTag,
            {
                'class': this.classObject
            },
            this.$slots.default )
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'row'
         * */
        classPrefix: {
            type: String,
            default: 'row'
        },
        /**
         * textbaseline
         * @property align
         * @type String
         * */
        align: {
            type: String,
            default: 'stretch',
            validator(val){
                return ['top','bottom','center','stretch','baseline'].indexOf(val) >= 0
            }
        },
        /**
         * ,ListItem
         * @property componentTag
         * @type String
         * @default 'div'
         * */
        componentTag: {
            type: String,
            default: 'div'
        }
    }
}



