import SwBase from '../Base.vue'
import toggle from '../../mixins/toggle'
export default {
    name: 'SwPopup',
    extends: SwBase,
    mixins: [toggle],
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'popup'
         * */
        classPrefix: {
            type: String,
            default: 'popup'
        },
        /**
         * ,['top','bottom']
         * @property align
         * @type String
         * @default 'top'
         * */
        align: {
            type: String,
            default: 'top'
        }
    }
}



