/**
 * <br/>
 * - srcalt
 * - defaultSrc
 * - phSizedefaultcovercontain
 * - lazyImageList
 * - covercontain
 *
 * 
 * - src: <br/>
 * - defaultSrc: <br/>
 * - phSize: <br/>
 * - lazy: <br/>
 *
 * 
 * - load: 
 *
 * @class Image
 * @module 
 * @extends Component
 * @constructor
 * @since 0.0.1
 * @demo image|image.js {}
 * @show true
 * */
import SwBase from '../Base.vue'
import { getClientHeight,getOffsetTop ,getWindowScrollTop} from '../../utils/tool.js'
export default {
    name: 'SwImage',
    extends: SwBase,
    data(){
        return {
            load: false,
            currentSrc: this.defaultSrc
        }
    },
    methods: {
        handleScroll(){
            this.lazyLoadImage()
        },
        handleLoadSuccess(img){
            this.imageWidth = img.width
            this.imageHeight = img.height

            this.lazy && window.removeEventListener('scroll', this.handleScroll)
            this.currentSrc = this.src

            this.image = this.$refs.imageRef
            switch(this.phSize){
                case 'cover':
                    if(this.imageWidth > this.imageHeight){
                        this.image.style.height = '100%'
                        this.image.style.width = this.imageWidth
                    }
                    this.imageWidth > this.imageHeight ? this.image.style.height = '100%' :  this.image.style.width = '100%'
                    break
                case 'contain':
                    this.imageWidth > this.imageHeight ? this.image.style.width = '100%' : this.image.style.height = '100%'
                    break
                default:
                    this.image.style.width = '100%'
                    this.image.style.height = '100%'
            }
            this.$emit('load')
        },
        lazyLoadImage(){
            this.scrollTop = getWindowScrollTop()
            this.bodyHeight = getClientHeight()
            this.imageTop = getOffsetTop(this.$refs.imageParentRef)
            //this.scrollTop + this.bodyHeight + this.bodyHeight/2 >= this.imageTop
            if(!this.load && (this.scrollTop + this.bodyHeight >= this.imageTop)){
                this.loadImage()
            }
        },
        loadImage(){
            try{
                let img = new Image()
                img.src = this.src
                this.load = true

                if(img.complete){
                    this.handleLoadSuccess(img)
                    return
                }
                img.onload = ()=>{
                    img.onload = null
                    this.handleLoadSuccess(img)
                }
            }catch(err){
                this.$emit('load',err)
            }
        }
    },
    created(){
        if(this.defaultSrc){
            this.defaultImg = new Image()
            this.defaultImg.src = this.defaultSrc
        }
    },
    mounted(){
        this.lazy && window.addEventListener('scroll', this.handleScroll)
        if(this.defaultImg && !this.defaultImg.complete){ //
            this.defaultImg.onload = ()=>{
                this.lazy ? this.lazyLoadImage() : this.loadImage()
            }
            return
        }
        this.lazy ? this.lazyLoadImage() : this.loadImage()
    },
    destroyed(){
        window.removeEventListener('scroll', this.handleScroll)
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'image'
         * */
        classPrefix: {
            type: String,
            default: 'image'
        },
        /**
         * 
         * @property defaultSrc
         * @type String
         * @default null
         * */
        defaultSrc: String,
        /**
         * 
         * @property src
         * @type String
         * @default
         * */
        src: String,
        /**
         * 
         * @property alt
         * @type String
         * @default
         * */
        alt: String,
        /**
         * [default, cover, contain]
         * @property phSize
         * @type String
         * @default 'default'
         * */
        phSize: {
            type: String,
            default: 'default'
        },
        /**
         * 
         * @property lazy
         * @type Boolean
         * @default false
         * */
        lazy: {
            type: Boolean,
            default: false
        }
    }
}



