/**
 * https://future-team.github.io/gfs-icons/index.html
 */
import SwBase from '../Base.vue'
export default {
    name: 'SwIcon',
    extends: SwBase,
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'icon'
         * */
        classPrefix: {
            type: String,
            default: 'icon'
        },
        /**
         * icon
         * @property phIcon
         * @type String
         * @default ''
         **/
        phIcon: {
            type: String,
            default: ''
        },
        /**
         * [primarywarningdangerinfoerrorsuccess], primary
         * @property phStyle
         * @type Boolean
         * @default 'default'
         * */
        phStyle: {
            type: String,
            default: 'default'
        },
        /**
         * [xssmmdlgxlg], sm
         * @property phSize
         * @type String
         * @default 'sm'
         * */
        phSize: {
            type: String,
            default: 'sm'
        }
    }
}



