import SwDialog from './Dialog.vue'
import SwButtonGroup from '../button/ButtonGroup.vue'
import SwButton from '../button/Button.vue'
import SwInput from '../input/Input.vue'
import toggle from '../../mixins/toggle'
export default {
    name: 'SwPrompt',
    mixins: [toggle],
    components: {
        SwButton,
        SwButtonGroup,
        SwDialog,
        SwInput
    },
    data(){
        return {
            inputValue: ' ',
            currentVisible: this.visible
        }
    },
    computed: {
        currentButtons(){
            return this.buttons ? this.buttons : [
                {text: '', onHandle: this.handleToggle.bind(this.false)},
                {text: '', onHandle: this.handleConfirm}
            ]
        },
        buttonsType(){
            return this.currentButtons.length>2
        }
    },
    methods: {
        handleConfirm(evt){
            this.$emit('on-confirm',this.inputValue,evt)
        },
        handleClick(onHandle){
            onHandle ? onHandle(this.inputValue) : this.handleToggle.bind(this.false)
        },
        handleInput(value){
            this.inputValue = value
        }
    },
    watch: {
        visible(val){
            this.currentVisible = val
        },
        currentVisible(val){
            this.inputValue = ''
            this.$emit('toggle',val)
        }
    },
    props: {
        /**
         * 
         * @property title
         * @type String
         * */
        title: String,
        /**
         * 
         * @property title
         * @type String
         * */
        content: null,
        /**
         * 
         * @property buttons
         * @type Array
         * */
        buttons: Array,
        /**
         * , 
         * @property closeButton
         * @type Boolean
         * */
        closeButton: Boolean,
        /**
         * , closeCallback
         * @property shadowDisabled
         * @type Boolean
         * */
        shadowDisabled: Boolean,
    }
}



