import SwBase from '../Base.vue'
export default {
    name: 'SwButtonGroup',
    extends: SwBase,
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'button-group'
         * */
        classPrefix : {
            type: String,
            default: 'button-group'
        },
        /**
         * default()justify()tacked()segmente()footer()
         * @property phType
         * @type String
         * @default 'default'
         * */
        phType: {
            type: String,
            default: 'default'
        }
    }
}



