/**
 * <br/>
 * - Pullup
 * - mode [auto,button] 2
 * - status243
 * - tips['','','','','']status
 * - phStylemodeauto
 *
 * 
 * - mode:auto
 * - status::0, 1, 2, 3, 4
 * - tips:['','','','','']
 * - phStyle:'primary'
 *
 * 
 * - load
 *
 * 
 * ```code
 *  // 
 *  <sw-pullup :status="status" @load="handleLoad"></sw-pullup>
 * ```
 *
 * @class Pullup
 * @module 
 * @extends Component
 * @constructor
 * @since 0.0.1
 * @demo pullup {}
 * @show true
 * */
import SwBase from '../Base.vue'
import SwButton from '../button/Button.vue'
import SwIcon from '../icon/Icon.vue'
import { getClientHeight } from '../../utils/tool.js'
export default {
    name: 'SwPullup',
    extends: SwBase,
    components: {
        SwIcon,
        SwButton
    },
    data(){
        return {
            currentStatus: this.status,
            touchBottom: false,
            distanceY: 0
        }
    },
    methods: {
        handleLoad(evt){
            this.$emit('load',evt)
        },
        handleScroll(){
            this.scrollTop = document.body.scrollTop
            this.clientHeight = getClientHeight()
            this.pullupElemOffsetTop = this.$refs.pullupRef.offsetTop
            if(this.scrollTop + this.clientHeight >= this.pullupElemOffsetTop){
                this.touchBottom = true
                if(this.currentStatus === 1 || this.currentStatus === 4){ //   
                    return
                }
                this.handleLoad()
            }else{
                this.touchBottom = false
            }
        }
    },
    watch: {
        status(val){
            this.currentStatus = val
        }
    },
    mounted(){
        this.mode !== 'button' && window.addEventListener('scroll', this.handleScroll)
    },
    destroyed(){
        this.mode !== 'button' && window.removeEventListener('scroll', this.handleScroll)
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'pullup'
         * */
        classPrefix: {
            type: String,
            default: 'pullup'
        },
        /**
         * [auto,button], auto
         * @property mode
         * @type String
         * @default 'auto'
         **/
        mode: {
            type: String,
            default: 'auto'
        },
        /**
         * 0, 1, 2, 3, 4
         * @property status
         * @type Number
         * @default 4
         **/
        status: {
            type: Number,
            default: 4
        },
        /**
         * 5
         * @property tips
         * @type Array
         * @default ['','','','','']
         **/
        tips: {
            type: Array,
            default: function(){
                return ['','','','','']
            }
        },
        /**
         * primary
         * @property phStyle
         * @type String
         * @default 'primary'
         **/
        phStyle: {
            type: String,
            default: 'primary'
        }
    }
}



