import SwBase from '../Base.vue'
export default {
    name: 'SwSwitch',
    extends: SwBase,
    methods: {
        handleClick(evt){
            this.$emit('click',this.checked,evt)
        }
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'switch'
         * */
        classPrefix: {
            type: String,
            default: 'switch'
        },
        /**
         * 
         * @property checked
         * @type Boolean
         * */
        checked: {
            type: Boolean,
            default: false
        }
    }
}



