import SwBase from '../Base.vue'
export default {
    name: 'SwCol',
    extends: SwBase,
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'col'
         * */
        classPrefix: {
            type: String,
            default: 'col'
        },
        /**
         * 1005-1005331\366(2\3)
         * @property width
         * @type Number
         * */
        width: Number,
        /**
         * 
         * @property offset
         * @type Number
         * */
        offset: Number,
        /**
         *  ['top', 'bottom','center']
         * @property align
         * @type String
         * */
        align: {
            type: String
        }
    }
}



