import SwDialog from './Dialog.vue'
import SwButtonGroup from '../button/ButtonGroup.vue'
import SwButton from '../button/Button.vue'
import toggle from '../../mixins/toggle'
export default {
    name: 'SwAlert',
    mixins: [toggle],
    data(){
        return {
            currentVisible: this.visible
        }
    },
    components: {
        SwButton,
        SwButtonGroup,
        SwDialog
    },
    methods: {
        handleClick(onHandle){
            onHandle ? onHandle() : this.handleToggle(false)
        }
    },
    computed: {
        currentButtons(){
            return this.buttons ? this.buttons : [{text: '', onHandle: this.handleToggle.bind(this,false)}]
        },
        buttonsType(){
            return this.currentButtons.length>2
        }
    },
    watch: {
        visible(val){
            this.currentVisible = val
        },
        currentVisible(val){
            this.$emit('toggle',val)
        }
    },
    props: {
        /**
         * , 
         * @property closeButton
         * @type Boolean
         * */
        closeButton: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property shadowDisabled
         * @type Boolean
         * */
        shadowDisabled: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property title
         * @type String
         * */
        title: null,
        /**
         * 
         * @property title
         * @type String
         * */
        content: null,
        /**
         * 
         * @property buttons
         * @type Array
         * */
        buttons: Array
    }
}



