import SwBase from '../Base.vue'
export default {
    name: 'SwSteps',
    extends: SwBase,
    data(){
        return {
            currentStep_ : this.currentStep
        }
    },
    methods: {
        handleClick(item,index){
            if(this.readOnly){return}
            this.$emit('click',item,index)
        }
    },
    watch: {
        currentStep(val){
            this.currentStep_ = val
        }
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'steps'
         * */
        classPrefix: {
            type: String,
            default: 'steps'
        },
        /**
         * 
         * @property readOnly
         * @type Boolean
         * @default false
         **/
        readOnly: {
            type: Boolean,
            default: false
        },
        /**
         * stepslist
         * @property list
         * @type Array
         * @default []
         **/
        list: {
            type: Array,
            default: ()=>[]
        },
        /**
         * 0currentStep1currentSteplist
         * @property currentStep
         * @type Number
         * @default 0
         **/
        currentStep: {
            type: Number,
            default: 0
        }
    }
}



