import SwBase from '../Base.vue'
export default {
    name: 'SwTextarea',
    extends: SwBase,
    data(){
        return {
            currentValue : this.value
        }
    },
    computed: {
        inputLength(){
            return this.currentValue ? this.currentValue.length : 0
        }
    },
    methods: {
        handleChange(evt){
            this.currentValue = evt.target.value
            this.$emit('input',this.currentValue,evt)
            this.$emit('change',this.currentValue,evt)
        }
    },
    watch: {
        value(val){
            this.currentValue = val
        }
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'textarea'
         * */
        classPrefix: {
            type: String,
            default: 'textarea'
        },
        /**
         * 
         * @property value
         * @type String
         * */
        value: String,
        /**
         * 
         * @property count
         * @type Boolean
         * */
        count: Boolean,
        /**
         * 
         * @property maxLength
         * @type Number
         * */
        maxlength: Number,
        /**
         * 
         * @property placeholder
         * @type String
         * */
        placeholder: String
    }
}



