import SwBase from '../Base.vue'
import SwIcon from '../icon/Icon.vue'
export default {
    name: 'SwToast',
    extends: SwBase,
    components: {SwIcon},
    data(){
        return {
            /**
             * ,
             * @property visible
             * @type Boolean
             * @default false
             **/
            visible: false,
            /**
             * 
             * @property duration
             * @type Number
             * @default 2000
             **/
            duration: 2000,
            /**
             * icon
             * @property phIcon
             * @type String
             * @default ''
             **/
            phIcon: '',
            /**
             * 
             * @property content
             * @type String
             * @default ''
             **/
            content: '',
            /**
             * 
             * @property callback
             * @type Function
             * @default
             **/
            callback: Function
        }
    },
    computed: {
        isLoading(){
            return this.phIcon.indexOf('loading') !== -1
        }
    },
    methods: {
        startTimer(){
            this.visible = true
            if(this.duration > 0){
                this.timer = setTimeout(() => {
                    if (this.visible) {
                        this.visible = false
                        this.remove()
                    }
                }, this.duration)
            }
        },
        remove(){
            this.callback && this.callback()
            clearTimeout(this.timer)
            this.$destroy()
            this.$el.parentNode.removeChild(this.$el)
        }
    },
    mounted(){
        this.startTimer()
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'toast'
         * */
        classPrefix: {
            type: String,
            default: 'toast'
        }
    }
}



