import { warning } from './tool'

let getVal= function(vals,val){
    let result=(vals.join(',')+',').match(val.toLowerCase()+',' )!=null ? val: ''
    if(!result){
        warning(' %s  %s ',val , vals.join(','))
    }
    return result
}

export default {

    /**/
    phType:function(val){
        return getVal(['default','tacked','justify','segmente'],val)
    },
    phSize:function(val){
        return getVal(['default','xlg','lg','md','sm','xs','cover','contain'],val)
    },
    phStyle:function(val){
        return getVal(['default','primary','info','success','error','warning','danger','link','gray','light','positive','calm','balanced','energized','assertive','lightbrown','lightgray'],val)
    },
    phStatus:val=>{
        getVal(['diabled','active','enable'],val)
    },
    align:function(val){
        return getVal(['center','left','middle','auto','top','right','bottom','stretch','baseline'],val)
    },
    placement:function(val){
        return getVal(['top','bottom','right','left','left-full','right-full','full-screen'],val)
    },

    /**/
    disabled:true,
    active:true,
    radius:true,
    round:true,
    clearfix:true,
    clear:true,
    phHref:true,
    show:true,
    hide:true,
    block:true,
    hollow: true,
    error: true,
    heading: true,
    tail: true,
    required: true,
    stable:true,
    visible:true,

    /*csszIndex="2",mergestylezIndexprops*/
    zIndex:val=>{
        if(typeof(val) =='string' ){
            return {
                zIndex:val
            }
        }
        return 'zindex'
    },
    border:val=>{
        if(typeof(val) =='string' ){
            return {
                border:val
            }
        }
        return  'border'
    },
    padding:val=>{
        if(typeof(val) =='string' ){
            return {
                padding:val
            }
        }
        return 'padding'
    },
    margin:val=>{
        if(typeof(val) =='string' ){
            return {
                margin:val
            }
        }
        return 'margin'
    },
    radiusSize:val=>{
        return {
            WebkitBorderRadius:val,
            borderRadius:val
        }
    },
    width:function(val){
        return ''+val
    },
    offset:function(val){
        return 'offset-'+val
    }
}



