export default {
    name: 'SwCheckbox',
    methods: {
        handleChange(evt){
            this.$emit('change',evt.target.value,evt)
        }
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'input'
         * */
        classPrefix: {
            type: String,
            default: 'input'
        },
        /**
         * 
         * @property label
         * @type String
         * @default ''
         * */
        label: {
            type: String,
            default: ''
        },
        /**
         * 
         * @property type
         * @type String
         * @default checkbox
         * */
        type: {
            type: String,
            default: 'checkbox'
        },
        /**
         * 
         * @property disabled
         * @type Boolean
         * @default false
         * */
        disabled: Boolean,
        /**
         * 
         * @property name
         * @type String
         * @default ''
         * */
        name: String,
        /**
         * 
         * @property checked
         * @type Boolean
         * @default false
         * */
        checked: Boolean,
        /**
         * 
         * @property value
         * @type
         * @default false
         * */
        value: null
    }
}



