import SwBase from '../Base.vue'
import SwIcon from '../icon/Icon.vue'
import toggle from '../../mixins/toggle'
export default {
    name: 'SwAccordion',
    extends: SwBase,
    mixins: [toggle],
    components: {SwIcon},
    methods:{
        setHeight(){
            this.$refs.accordionBodyParent.style.height = this.visible ? this.height : 0
        }
    },
    data(){
        return {
            height: 0
        }
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'accordion'
         * */
        classPrefix: {
            type: String,
            default: 'accordion'
        },
        /**
         * 
         * @property hideIcon
         * @type Boolean
         * @default false
         * */
        hideIcon : {
            type: Boolean,
            default: false
        }
    },
    mounted(){
        this.height = this.$refs.accordionBody.offsetHeight+'px'
        this.setHeight()
    },
    watch: {
        visible(){
            this.setHeight()
        }
    }
}



