import SwBase from '../Base.vue'
import SwIcon from '../icon/Icon.vue'
export default {
    name: 'SwInput',
    extends: SwBase,
    components: { SwIcon },
    data() {
        return {
            currentType: this.type,
            canSee: false,
            focus: false,
            currentValue: this.value
        }
    },
    computed: {
        clearStatus(){
            return this.clear && this.currentValue && this.focus
        },
        visibleStatus(){
            return this.visible && this.type === 'password'
        },
        errorState(){
            return this.error && !this.focus
        },
        nativeProps(){
            let props = ['name','maxlength','minlength','disabled','autocomplete','autofocus','min','max','readonly']
            let nativeProps = {}
            props.forEach((key)=>{
                if(this.$props[key]){
                    nativeProps[key] = this.$props[key]
                }
            })
            return nativeProps
        }
    },
    methods: {
        handleChange(evt){
            this.focus = true
            let value = evt.target.value
            this.currentValue = value
            this.$emit('input',value,evt)
            this.$emit('change',value,evt)
        },
        handleFocus(){
            this.focus = true
            this.$emit('focus')
        },
        handleBlur(){
            this.$emit('blur')
            //iconhandleBlurthis.focusfalse,handleClearsetTimeout
            this.timer = setTimeout(()=>{
                this.focus = false
            })
        },
        handleClear(){
            this.timer && clearTimeout(this.timer)
            this.currentValue = ''
            this.$refs.inputRef.focus()
        },
        handleVisible(){
            this.canSee = !this.canSee
            this.currentType = this.canSee ? 'text': 'password'
        }
    },
    watch: {
        value(val){
            this.currentValue = val
        }
    },
    beforeDestroy(){
        this.timer && clearTimeout(this.timer)
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'input'
         * */
        classPrefix: {
            type: String,
            default: 'input'
        },
        /**
         * input, checkboxradioinput, text
         * @property type
         * @type String
         * @default 'text'
         * */
        type: {
            type: String,
            default: 'text',
            validator: function(val){
                return ['checkbox', 'radio'].indexOf(val) < 0
            }
        },
        /**
         * []text,search,password
         * @property clear
         * @type Boolean
         * */
        clear: Boolean,
        /**
         * []password
         * @property visible
         * @type Boolean
         * */
        visible: Boolean,
        /**
         * 
         * @property error
         * @type Boolean
         * */
        error: Boolean,
        /**
         * icon
         * @property phIcon
         * @type String
         * @default ''
         **/
        phIcon: {
            type: String,
            default: ''
        },
        /**
         * input
         * @property phIcon
         * @type String
         * @default ''
         **/
        placeholder: String,
        disabled: Boolean,
        maxlength: Number,
        minlength: Number,
        autocomplete: String,
        autofocus: Boolean,
        readonly:Boolean,
        value: null,
        name: null,
        min:null,
        max:null

    }
}



