import SwBase from '../Base.vue'
export default {
    name: 'SwBadge',
    extends: SwBase,
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'badge'
         * */
        classPrefix: {
            type: String,
            default: 'badge'
        },
        /**
         * [defaultprimarywarningdangerinfoerrorsuccess], primary
         * @property phStyle
         * @type string
         * @default 'default'
         **/
        phStyle: {
            type: String,
            default: 'primary'
        }
    }
}



