import SwBase from '../Base.vue'
import SwIcon from '../icon/Icon.vue'
export default {
    name: 'SwButton',
    extends: SwBase,
    components: {SwIcon},
    methods: {
        handleClick(evt){
            this.$emit('click',evt)
        }
    },
    props:{
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'button'
         * */
        classPrefix: {
            type: String,
            default: 'button'
        },
        /**
         * [smmdlg], sm
         * @property phSize
         * @type String
         * @default 'sm'
         * */
        phSize: {
            type: String,
            default: 'sm'
        },
        /**
         * [primarywarningdangerinfoerrorsuccesslinkgray], primary
         * @property phStyle
         * @type Boolean
         * @default 'primary'
         * */
        phStyle: {
            type: String,
            default: 'primary'
        },
        /**
         * 
         * @property block
         * @type Boolean
         * @default false
         * */
        block: Boolean,
        radius: Boolean,
        /**
         * 
         * @property hollow
         * @type Boolean
         * @default false
         * */
        hollow: Boolean,
        /**
         * 
         * @property disabled
         * @type Boolean
         * @default false
         * */
        disabled: Boolean,
        /**
         * 
         * @property active
         * @type Boolean
         * @default false
         * */
        active: Boolean,
        /**
         * icon
         * @property phIcon
         * @type string
         * @default ''
         **/
        phIcon: String,
        /**
         * icon
         * @property stable
         * @type Boolean
         * @default false
         * */
        stable: Boolean
    }
}



