import SwBase from '../Base.vue'
import Row from '../grid/Row.vue'
export default {
    name: 'SwListItem',
    extends: SwBase,
    components: {Row},
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'list-item'
         * */
        classPrefix: {
            type: String,
            default: 'list-item'
        },
        /**
         * 
         * @property disabled
         * @type Boolean
         * @default false
         * */
        disabled: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property error
         * @type Boolean
         * @default false
         * */
        error: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property required
         * @type Boolean
         * @default false
         * */
        required: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property navigate
         * @type Boolean
         * @default false
         * */
        navigate: {
            type: Boolean,
            default: false
        }
    }
}



