/**
 * / v-model
 */
export default {
    model: {
        prop: 'visible',
        event: 'toggle'
    },
    methods: {
        handleToggle(val,evt){
            this.$emit('toggle',val,evt)
        }
    },
    props: {
        /**
         * 
         * @property visible
         * @type Boolean
         * */
        visible: {
            type: Boolean,
            default: false
        }
    }
}


