import SwBase from '../Base.vue'
import toggle from '../../mixins/toggle'
export default {
    name: 'SwDialog',
    extends: SwBase,
    mixins: [toggle],
    methods: {
        handleShadowClose(){
            !this.shadowDisabled && this.handleToggle(false)
        }
    },
    props: {
        /**
         * 
         * @property classPrefix
         * @type String
         * @default 'dialog'
         * */
        classPrefix: {
            type: String,
            default: 'dialog'
        },
        /**
         * , 
         * @property closeButton
         * @type Boolean
         * */
        closeButton: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property shadowDisabled
         * @type Boolean
         * */
        shadowDisabled: {
            type: Boolean,
            default: false
        },
        /**
         * 
         * @property title
         * @type String
         * */
        title: String,
    }
}



